<template>
    <div v-if="ready" style='position:relative'>
        <form id='form' enctype="multipart/form-data" @submit.prevent="handleSubmit">
            <div class="card">
                <div class="card-header">
                    <strong>Swap Records</strong>
                </div>    
                <div class="card-body">
                    <div class="col-12 flex" style='margin-bottom:0.75rem;padding-right:0'>
                        <div class="col-4"></div>
                        <div class="col-4 flex" style='padding-right:0'>
                            <div class="col-5"></div>
                            <div class="col-3" style='padding-top:0.35rem'>
                                <label>Order By:</label>
                            </div>
                            <div class="col-4" style='padding-right:0'>
                                <select class="form-control select" v-model="order" @change="change_view">
                                    <option value="day">Day</option>
                                    <option value="date">Date</option>
                                </select>
                            </div>
                        </div>
                        <div class="col-4 flex" style='padding-right:0'>
                            <div class="col-2"></div>
                            <div class="col-4" style='padding-top:0.35rem'>
                                <label>Select Date:</label>
                            </div>
                            <div class="col-6" style='padding-right:0'>
                                <input type="date" class="form-control" v-model="tdy_date" @change="new_day"/>
                            </div>
                        </div>                      
                    </div>
                    <div v-if="open_list" class="inner_overlay">
                        <div class="card" style='height:100%'>
                            <div class="card-header">
                                <div class="flex">
                                    <strong>Swap Records</strong>
                                    <i class="fa fa-window-close" aria-hidden="true" style='margin-left:auto;margin-top:0.25rem;margin-right:0.5rem;cursor:pointer' @click="close_overlay"></i>
                                </div>
                            </div>                        
                            <div class="card-body">
                                <div class="flex" style='border-bottom:1px solid rgba(60,60,60,0.5)'>
                                    <div class="col-4"><label>Requester</label></div>
                                    <div class="col-3"><label>Lesson</label></div>
                                    <div class="col-2"><label>Subject</label></div>                                   
                                    <div class="col-1"><label>Location</label></div>
                                    <div class="col-2"><label>Action</label></div>
                                </div>
                                <div class="flex recordbar" v-for="n in count_request" style='border-bottom:1px solid rgba(60,60,60,0.5)'>
                                    <div class="col-4">{{n}}. Request Swap From : <b>{{ from_name[n-1] }}</b></div>
                                    <div class="col-3">{{ from_date[n-1] }}</div>
                                    <div class="col-2">{{ from_class[n-1] }} - {{ from_subject[n-1] }}</div>                                   
                                    <div class="col-1">{{ from_location[n-1] }}</div>
                                    <div class="col-2 flex">
                                        <div class="col-6">
                                            <i class="fa fa-check" aria-hidden="true" style='cursor: pointer;' @click="accept(n-1)"></i>
                                        </div>
                                        <div class="col-6">
                                            <i class="fa fa-ban" aria-hidden="true" style='cursor: pointer;' @click="decline(n-1)"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="card-footer">
                                <button type="submit" class="btn btn-primary btn-sm">
                                    <i class="fa fa-paper-plane"></i> Submit
                                </button>
                                <button type="reset" class="btn btn-danger btn-sm" @click="close_overlay">
                                    <i class="fa fa-ban"></i> Cancel
                                </button>
                            </div>
                        </div>
                    </div>
                    <table class="table table-striped first-td-padding">
                        <thead>
                            <tr v-if="order=='day'&&this.ready_change">
                                <th style="width:5.8%">Period</th>
                                <th>Day 1<br/><u style='font-size:90%'>{{ cycle_date[0] }}</u></th>
                                <th>Day 2<br/><u style='font-size:90%'>{{ cycle_date[1] }}</u></th>
                                <th>Day 3<br/><u style='font-size:90%'>{{ cycle_date[2] }}</u></th>
                                <th>Day 4<br/><u style='font-size:90%'>{{ cycle_date[3] }}</u></th>
                                <th>Day 5<br/><u style='font-size:90%'>{{ cycle_date[4] }}</u></th>
                                <th>Day 6<br/><u style='font-size:90%'>{{ cycle_date[5] }}</u></th>
                            </tr>
                            <tr v-if="order=='date'&&this.ready_change">
                                <th style="width:5.8%">Period</th>
                                <th>{{ cycle_date[0] }}<br/><u style='font-size:90%'>Day {{ by_day[0] }}</u></th>
                                <th>{{ cycle_date[1] }}<br/><u style='font-size:90%'>Day {{ by_day[1] }}</u></th>
                                <th>{{ cycle_date[2] }}<br/><u style='font-size:90%'>Day {{ by_day[2] }}</u></th>
                                <th>{{ cycle_date[3] }}<br/><u style='font-size:90%'>Day {{ by_day[3] }}</u></th>
                                <th>{{ cycle_date[4] }}<br/><u style='font-size:90%'>Day {{ by_day[4] }}</u></th>
                                <th>{{ cycle_date[5] }}<br/><u style='font-size:90%'>Day {{ by_day[5] }}</u></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="i in 8">
                                <td style='point-events:none;width:5.8%;text-align:center'><b>{{ dp_period[i-1] }}</b></td>
                                <td v-for="j in 6" :style="style[j-1][i-1]" @click="swap((j-1),(i-1))">                                   
                                    <b>{{ subject[j-1][i-1] }}</b><br/>{{ message[j-1][i-1] }}<br/>
                                    <div class="flex" style='margin-top:0.55rem'>
                                        <div style='float:left'>{{ class_a[j-1][i-1] }}</div>
                                        <div style='float:right;margin-left:auto;'>{{ location[j-1][i-1] }}</div>
                                    </div>
                                </td>                                   
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </form>     
    </div>   
</template>
<script>
export default {
    data(){
        return{
            dp_period:['Class Teacher','1','2','3','4','5','6','7'],
            order:'day',
            ready_change:false,
            by_day:['','','','','',''],
            day_list:['','','','','',''],
            from_name:[],
            from_date:[],
            from_subject:[],
            from_class:[],
            from_location:[],
            from_stat:[],
            from_id:[],
            count_request:0,
            return_list:[],
            tmp_date:'',
            message:[],
            swap_loc:[],
            swap_sub:[],
            swap_date:[],
            swap_day:[],
            swap_session:[],
            old_session:'',
            original_date:'',
            cycle_date:[],
            swap_subject_code:'',
            loc_list:[],
            sup1:'',
            sup2:'',
            sup3:'',
            app_type:'swap',
            selected_cls:'',
            selected_loc:'',
            subject_list:[],
            subject_code:'',
            type:'official',
            user_check:'',
            ava_list:[],
            select_day:'',
            select_session:'',
            target_date:'',
            open_list:false,
            tdy_date:'',
            style:[],
            tdy_day:'',
            lesson:[],   //format : lesson[session][day]
            subject:[],
            location:[],
            class_a:[],
            ready:false,
        }
    },
    watch: {
      tdy_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       },
       tmp_date: function (newDate, oldDate) {
         this.original_date = oldDate;
       }
    },   
    async created(){
        var today = new Date();
        var dd = String(today.getDate()).padStart(2, '0');
        var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        var yyyy = today.getFullYear();
            today = yyyy + '-' + mm + '-' + dd;
        this.tdy_date = today;      
        var i = 0;
        var j = 0;
        for(i=0;i<8;i++){
            this.message[i] = [];
            this.style[i] = [];
            this.lesson[i] = [];
            this.subject[i] = [];
            this.location[i] = [];
            this.class_a[i] = [];
            for(j=0;j<8;j++){
                this.message[i][j] = '';
                this.style[i][j] = 'pointer-events:none';
                this.lesson[i][j] = '';
                this.subject[i][j] = '';
                this.location[i][j] = '';
                this.class_a[i][j]='';
            }
        }
        await this.fetch_cycle();
        await this.fetch_subject();
        await this.fetch_loc();
        await this.new_day();       
        /* 
        await this.check_sub_date();
        await this.fetch_timetable();      
        await this.check_swap();
        await this.fetch_stat();*/
        this.ready_change = true;
        this.ready = true;
    },
    async mounted(){
        
    },
    methods:{
        async change_view(){
            this.ready_change = false;
                var j = 0;
                var m = 0;
                for(j=0;j<6;j++){
                    this.cycle_date[j]='';
                    this.by_day[j]='';
                    for(m=0;m<8;m++){
                        this.lesson[j][m] = '';
                        this.subject[j][m] = '';
                        this.class_a[j][m] = '';
                        this.location[j][m] = '';
                        this.style[j][m] = 'pointer-events:none';
                    }
                }
                if(this.order=='date'){
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                await axios.post('subsitude/sub/backend/change_view.php',formData)
                    .then(
                        (response)=>{
                            this.day_list = response.data;
                            var i = 0;
                            var s = 0;
                            for(i=0;i<this.day_list.length;i++){
                                this.cycle_date[i] = this.day_list[i].date;
                                this.by_day[i] = this.day_list[i].day;
                                s=0;
                                for(s=0;s<8;s++){
                                    if(this.tdy_date == this.cycle_date[i]){
                                        this.style[i][s]="pointer-events: none;background-color:rgba(178, 255, 186, 0.75)";
                                    }
                                    this.lesson[i][s] = this.day_list[i][s].subject;
                                    this.subject[i][s] = this.day_list[i][s].subject;
                                    this.class_a[i][s] = this.day_list[i][s].class;
                                    this.location[i][s] = this.day_list[i][s].classroom;
                                }
                            }
                        }
                    )
                    /*
                    await this.check_overlay();
                    await this.check_swap();
                    await this.fetch_stat();*/
                    await this.new_day();
                    await this.check_overlay();
                    this.ready_change = true;
            } else {
                /*
                await this.fetch_cycle();
                await this.fetch_subject();
                await this.fetch_loc();
                await this.fetch_timetable();                
                await this.check_sub_date();
                await this.check_overlay();
                await this.check_swap();
                await this.fetch_stat();*/
                await this.new_day();
                this.ready_change = true;
            }
        },
        async check_overlay(){
            var i = 0;            
            for(i=0;i<6;i++){
                var formData = new FormData();
                formData.append('cycle_date',this.cycle_date[i]);
                await axios.post('subsitude/sub/backend/fetch_leave.php',formData)
                    .then(
                        (response)=>{
                            if(response.data!='empty'){
                                var n=0;
                                var k=0; //day
                                var s=0; //session
                                var l=0;
                                for(l=0;l<response.data.length;l++){
                                    if(this.order=='day'){
                                        k = response.data[l].day;
                                        s = response.data[l].session;
                                    } else if(this.order=='date'){
                                        for(n=0;n<6;n++){
                                            if(response.data[l].date == this.cycle_date[n]){
                                                k = n+1;
                                            }
                                        }
                                        s = response.data[l].session;
                                    }
                                    this.subject[k-1][s] = response.data[l].subject;
                                    this.class_a[k-1][s] = response.data[l].class;
                                    this.location[k-1][s] = response.data[l].location;
                                    this.message[k-1][s] = 'Leave type : '+response.data[l].type+' '+response.data[l].app_type+' by '+response.data[l].assigned_id;
                                    if(response.data[l].type=='sick'){
                                        this.style[k-1][s] = 'background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8);pointer-events:none';
                                    } else {
                                        this.style[k-1][s] = 'background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8);pointer-events:none';
                                    }
                                }
                            }
                        }
                    )
            }
        },
        async fetch_cycle(){
            await axios.get('subsitude/sub/backend/fetch_cycle.php')
            .then(
                (response)=>{
                    if(response.data!='empty'){
                        this.tdy_day = response.data[0].day;
                        var tmp_day = Number(this.tdy_day)-1;
                        var i = 0;
                        var clr ='';
                        for(i=0;i<8;i++){                     
                            if(i%2 == 0){
                                clr = 'rgba(145, 255, 156, 0.75)';
                            } else {
                                clr = 'rgba(178, 255, 186, 0.75)';
                            }
                            this.style[tmp_day][i] = 'pointer-events:none;background-color:'+ clr;
                        }
                    }
                }
            )
        },
        async fetch_subject(){
            await axios.get('subsitude/sub/backend/fetch_subject.php')
            .then(
                (response)=>{
                    this.subject_list = response.data;
                    this.subject_code = this.subject_list[0].subject_code;
                }
            )
        },
        async fetch_loc(){
            await axios.get('subsitude/sub/backend/fetch_loc.php')
            .then(
                (response)=>{
                    this.loc_list = response.data;
                }
            )
        },
        async check_swap(){
            var i = 0;
            var d = 0;
            var s = 0;
            for(i=0;i<6;i++){
                var formData = new FormData();
                formData.append('cycle_date',this.cycle_date[i]);
                await axios.post('subsitude/swap_view/backend/accepted_swap.php',formData)
                    .then(
                        (response)=>{
                            var i = 0;
                            for(i=0;i<response.data.length;i++){
                                d = Number(response.data[i].day)-1;
                                s = Number(response.data[i].session)-1;
                                this.message[d][s] = 'Swapped with '+response.data[i].swap_with;
                                this.style[d][s] = 'pointer-events:none;background-color:rgb(138, 155, 242);color:rgba(60, 60, 60, 0.7)';
                                this.lesson[d][s] = response.data[i].subject;
                                this.subject[d][s] = response.data[i].subject;
                                this.location[d][s] = response.data[i].location;
                                this.class_a[d][s]= response.data[i].class;
                            }
                        }
                    )
            }
        },
        async fetch_timetable(){
            await axios.get('subsitude/sub/backend/fetch_time_table.php')
                .then(
                    (response)=>{
                        var i = 0;
                        var j = 0;
                        var counter = 0;
                        for(i=0;i<6;i++){
                            for(j=0;j<8;j++){
                                this.lesson[i][j] = response.data[counter].subject;
                                if(this.lesson[i][j]==''){
                                    //this.check_sub_record(i,j);
                                } else {
                                    this.class_a[i][j] = response.data[counter].class;
                                    this.subject[i][j] = response.data[counter].subject;
                                    this.location[i][j] = response.data[counter].classroom;
                                }                            
                                counter++;
                            }
                        }
                    }
                ) 
        },
        accept(id){
            var formData = new FormData();            
            formData.append('id',this.from_id[id]);
            axios.post('subsitude/swap_view/backend/accept_req.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.open_list=false;
                            this.new_day();
                            this.$forceUpdate();
                        }
                    }
                )
        },
        decline(id){
            var formData = new FormData();
            formData.append('id',this.from_id[id]);
            axios.post('subsitude/swap_view/backend/decline_req.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.open_list=false;
                            this.new_day();
                            this.$forceUpdate();
                        }
                    }
                )
        },
        async fetch_stat(){
            var formData = new FormData();
            var i = 0;
            formData.append('order',this.order);
                for(i=0;i<6;i++){
                    formData.append('day_date'+i,this.cycle_date[i]);
                }            
                await axios.post('subsitude/swap_view/backend/fetch_request.php',formData)
                    .then(
                        (response)=>{
                            var m = 0;
                            var n = 0;
                            for(m=0;m<6;m++){
                                for(n=0;n<8;n++){
                                    if(response.data[m]!=undefined){
                                        this.return_list = response.data[m];
                                        if(this.return_list[n]!=undefined){
                                            if(this.return_list[n].sub=='request exist'){
                                                this.message[m][n] = 'Request(s) Exist(s)';
                                                this.style[m][n] = 'cursor: pointer;background-color:rgb(255, 228, 188)';
                                            }
                                            if(this.return_list[n].req=='requested'){
                                                this.message[m][n] = 'Swapped Request To :'+this.return_list[n].req_to;
                                                this.style[m][n] = 'pointer-events:none;background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8);';
                                            }  
                                            if(this.return_list[n].sick=='sick leave'){
                                                this.message[m][n] = 'Sick Leave';
                                                this.style[m][n] = 'pointer-events:none;background-color:rgb(170, 128, 255);color:rgba(255,255,255,0.8)';
                                            }                                
                                        }                                   
                                    }
                                }
                            }
                        }
                    )
        },
        async fetch_day(i){
            var date = this.swap_date[i];
            var old_date = this.tmp_date;
            var formData = new FormData();
            formData.append('date',this.swap_date[i]);
            await axios.post('subsitude/sub/backend/new_day.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='empty'){
                            this.$alert('Selected Date is not a cycle Day !');
                            this.swap_date[i] = old_date;
                            this.$forceUpdate();
                        } else {
                            this.swap_day[i] = response.data[0].day;
                            this.tmp_date = this.swap_date[i];
                            this.$forceUpdate();
                        }
                    }
                )
        },
async new_day(){
            var tmp = '';
            if(this.order=='day'){                
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                await axios.post('subsitude/sub/backend/new_day.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                tmp = '';
                                this.tdy_day = response.data[0].day;
                                var tmp_day = Number(this.tdy_day)-1;
                                var i = 0;
                                var clr ='';
                                var n = 0;
                                var m = 0;
                                for(n=0;n<6;n++){
                                    for(m=0;m<8;m++){
                                        this.style[n][m] = 'pointer-events:none';
                                    }
                                }
                                for(i=0;i<8;i++){                     
                                    if(i%2 == 0){
                                        clr = 'rgba(145, 255, 156, 0.75)';
                                    } else {
                                        clr = 'rgba(178, 255, 186, 0.75)';
                                    }
                                    this.style[tmp_day][i] = 'pointer-events:none;background-color:'+ clr;
                                }                                                      
                            } else {
                                tmp = 'empty'
                                this.$alert('Selected Date is not a cycle Day !');
                                this.tdy_date = this.original_date;
                                /*
                                var k=0;
                                var l=0;
                                for(k=0;k<8;k++){
                                    for(l=0;l<8;l++){
                                        this.style[k][l] = 'pointer-events:none';
                                    }
                                    
                                }*/
                            }
                        }
                    )
                    if(tmp!='empty'){
                        await this.check_sub_date();
                        await this.reload_timetable();
                        await this.check_overlay(); 
                        await this.fetch_stat(); 
                    }
                    this.$forceUpdate();
            } else {    //order date
                this.ready_change = false;
                var j = 0;
                var m = 0;
                for(j=0;j<6;j++){
                    this.cycle_date[j]='';
                    this.by_day[j]='';
                    for(m=0;m<8;m++){
                        this.lesson[j][m] = '';
                        this.subject[j][m] = '';
                        this.class_a[j][m] = '';
                        this.location[j][m] = '';
                        this.message[j][m] = '';
                        this.style[j][m] = 'pointer-events:none';
                    }
                }
                var formData = new FormData();
                formData.append('date',this.tdy_date);
                await axios.post('subsitude/sub/backend/change_view.php',formData)
                    .then(
                        (response)=>{
                            if(!(response.data=='empty')){
                                tmp = '';
                                this.day_list = response.data;
                                var i = 0;
                                var s = 0;
                                for(i=0;i<this.day_list.length;i++){
                                    this.cycle_date[i] = this.day_list[i].date;
                                    this.by_day[i] = this.day_list[i].day;
                                    s=0;
                                    for(s=0;s<8;s++){
                                        if(this.tdy_date == this.cycle_date[i]){
                                            this.style[i][s]="pointer-events:none;background-color:rgba(178, 255, 186, 0.75)";
                                        }
                                        this.lesson[i][s] = this.day_list[i][s].subject;
                                        this.subject[i][s] = this.day_list[i][s].subject;
                                        this.class_a[i][s] = this.day_list[i][s].class;
                                        this.location[i][s] = this.day_list[i][s].classroom;
                                    }
                                }
                            } else {
                                tmp = 'empty'
                                this.$alert('Selected Date is not a cycle Day !');
                                this.tdy_date = this.original_date;
                            }
                        }
                    )
                    if(tmp!='empty'){
                        await this.check_sub_date();
                        await this.reload_timetable();
                        await this.check_overlay();  
                        await this.fetch_stat();
                    }
                    this.$forceUpdate();
                    this.ready_change = true;
            }
        },
        async reload_timetable(){
            if(this.order=='day'){
                await axios.get('subsitude/sub/backend/fetch_time_table.php')   //day
                .then(
                    (response)=>{
                        var i = 0;
                        var j = 0;
                        var counter = 0;
                        for(i=0;i<6;i++){
                            for(j=0;j<8;j++){
                                this.lesson[i][j] = response.data[counter].subject;
                                if(this.lesson[i][j]==''){
                                    this.check_sub_record(i,j);
                                } else {
                                    this.message[i][j]='';
                                    this.class_a[i][j] = response.data[counter].class;
                                    this.subject[i][j] = response.data[counter].subject;
                                    this.location[i][j] = response.data[counter].classroom;
                                }                            
                                counter++;
                            }
                        }
                    }
                )
            } else {
                var i = 0;
                var j = 0;
                var d = 0;
                var s = 0;
                for(i=0;i<6;i++){
                    var formData = new FormData();
                    formData.append('date',this.cycle_date[i]);
                    await axios.post('subsitude/sub/backend/sub_by_date.php',formData)
                        .then(
                            (response)=>{
                                if(response.data!=''){
                                    for(j=0;j<response.data.length;j++){
                                        d = Number(response.data[j].day)-1;
                                        s = response.data[j].session;
                                        this.class_a[d][s] = response.data[j].class;
                                        this.subject[d][s] = response.data[j].subject;
                                        this.location[d][s] = response.data[j].location;
                                        this.message[d][s] = response.data[j].app_type + ' - ( From : ' + response.data[j].leave_id + ')';
                                        this.style[d][s] = 'background-color:rgb(138, 155, 242);color:rgba(60, 60, 60, 0.7);pointer-events:none';
                                    }
                                }
                            }
                        )
                }
            }
        },
        close_overlay(){
            this.open_list = false;
        },
        leave_type(){
            if(this.type=='sick'){              
                if(this.select_session!='Full day'){
                    this.old_session = this.select_session;
                    this.select_session = 'Full Day';
                }
                this.app_type ='sup';
            } else {
                this.select_session = this.old_session;
                this.app_type = 'swap';
            }
        },
        async check_sub_date(){
            var formData = new FormData();
            formData.append('date',this.tdy_date);
            await axios.post('subsitude/sub/backend/fetch_cycle_date.php',formData)
                .then(
                    (response)=>{
                        var i = 0;
                        for(i=0;i<response.data.length;i++){
                            if(!(response.data == 'No cycle day')){
                                if(!(response.data[i]==undefined)||!(response.data[i]==null)){
                                    var index = response.data[i].day;
                                    index = Number(index)-1;
                                    this.cycle_date[index] = response.data[i].date;
                                }
                            }
                        }
                    }
                )
        },
        async check_sub_record(i,j){
            var formData = new FormData();
            formData.append('cycle_date',this.cycle_date[i]);
            formData.append('day',(i+1));
            formData.append('session',j);
            await axios.post('subsitude/sub/backend/check_sub_info.php',formData)
                .then(
                    (response)=>{
                        if(response.data.length>0){
                            this.message[i][j] =response.data[0].app_type + ' - ( From : ' + response.data[0].leave_id + ')';
                            this.lesson[i][j] = response.data[0].subject;
                            this.subject[i][j] = response.data[0].subject;
                            this.class_a[i][j] = response.data[0].class;
                            this.location[i][j] = response.data[0].location;
                            this.style[i][j] = 'background-color:rgb(138, 155, 242);color:rgba(60, 60, 60, 0.7);pointer-events:none';
                        } else {
                            this.message[i][j] = '';
                            this.lesson[i][j] = '';
                            this.subject[i][j] = '';
                            this.class_a[i][j] = '';
                            this.location[i][j] = '';
                        }
                    }
                )
            this.$forceUpdate();
        },
        async swap(i,j){
            var l = 0;
            for(l=0;l<3;l++){
                this.swap_sub[l] = 'Select Subject';
                this.swap_loc[l] = 'Select Location';
                this.swap_date[l] = 'Select Date';
                this.swap_day[l] = 'Select Day';
                this.swap_session[l] = 'Select Session';
            }
            this.type = 'official';
            this.app_type = 'swap';
            var day = i+1;
            this.select_day = day;
            var session = j;
            this.select_session = session;
            var formData = new FormData();
            formData.append('sel_date',this.cycle_date[i]);
            formData.append('session',session);
            formData.append('day',day);
            formData.append('app_type',this.app_type);
            formData.append('class',this.class_a[i][j]);
            await axios.post('subsitude/swap_view/backend/fetch_date_request.php',formData)
                .then(
                    (response)=>{
                        this.count_request = response.data.length;
                        var i = 0;
                        for(i=0;i<this.count_request;i++){
                            this.from_name[i] = response.data[i].leave_id;
                            this.from_date[i] = response.data[i].date + " (Day:"+response.data[i].day+") "+"Period:"+response.data[i].session;
                            this.from_subject[i] = response.data[i].subject;
                            this.from_class[i] = response.data[i].class;
                            this.from_location[i] = response.data[i].location;
                            this.from_stat[i] = response.data[i].stat;
                            this.from_id[i] = response.data[i].id;
                        }
                    }
                )
                this.$forceUpdate();
            this.open_list=true;
            this.tmp_date = this.tdy_date;
        },
        async new_list(){
            var i = Number(this.select_day)-1;
            var j = this.select_session;
            var formData = new FormData();
            formData.append('sel_date',this.tdy_date);
            formData.append('session',this.select_session);
            formData.append('day',this.select_day);
            formData.append('app_type',this.app_type);
            formData.append('class',this.class_a[i][j]);
            await axios.post('subsitude/sub/backend/fetch_ava_teacher.php',formData)
                .then(
                    (response)=>{
                        this.ava_list = response.data;
                    }
                )
            this.check_sub_date();
            this.$forceUpdate();
        },
        handleSubmit(){
            var formData = new FormData();
            formData.append('sel_date',this.tdy_date);
            formData.append('day',this.select_day);
            formData.append('session',this.select_session);
            formData.append('original_subject',this.subject_code);
            formData.append('swap_subject',this.swap_subject_code);
            formData.append('class',this.selected_cls);
            formData.append('location',this.selected_loc);
            formData.append('type',this.type);
            formData.append('app_type',this.app_type);
            formData.append('sup1',this.sup1);
            formData.append('sup2',this.sup2);
            formData.append('sup3',this.sup3);
            //append swap info if is swap
            if(this.app_type=='swap'){
                var i = 0;
                for(i=0;i<3;i++){
                    formData.append('subject'+i,this.swap_sub[i]);
                    formData.append('location'+i,this.swap_loc[i]);
                    formData.append('date'+i,this.swap_date[i]);
                    formData.append('day'+i,this.swap_day[i]);
                    formData.append('session'+i,this.swap_session[i]);
                }
            }
            //
            axios.post('subsitude/sub/backend/apply_swap.php',formData)
                .then(
                    (response)=>{
                        if(response.data=="Successfully Submitted Application"){
                            this.$alert(response.data);
                            this.open_list = false;
                        } else {
                            this.$alert(response.data);
                        }
                    }
                )
        }
    }
}
</script>
<style scoped>
th{
    width:15.7%;
    text-align: center;
    font-size: 1.1rem;
}
td{
    padding-left:1.25rem;
    padding-right:1.25rem;
    border:2px solid #ffffff;
    height:5rem;   
    transition: all 0.3s ease-in-out;
}
td:hover{
    background-color:rgba(160, 237, 255,0.8)!important;
}
.inner_overlay{
    z-index: 5;
    width:65%;
    max-height:55%;
    position:absolute;
    margin-left:auto;
    margin-top:auto;
    margin-bottom:auto;
    margin-right:auto;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color:rgba(255, 255, 255, 0.95);
    box-shadow: 5px 5px 5px 5px rgba(211, 211, 211, 0.6);
}
.fa-window-close{
    transition: all 0.15s ease-in-out;
    color:rgba(255, 255, 255, 0.55);
}
.fa-window-close:hover{
    color:rgba(255, 255, 255, 1);
}
button.form-control:hover{
    color:#ffffff;
}
.recordbar{
    padding-top:0.5rem;
    padding-bottom:0.5rem;
    transition: all 0.3s ease-in-out;
    cursor:pointer;
}
.recordbar:hover{
    background-color:rgba(60,60,60,0.3);
}
.fa-check{
    color:rgba(0, 156, 34,0.7);
}
.fa-check:hover{
    color:rgba(0, 156, 34,1);
}
.fa-ban{
    color:rgba(242, 0, 0, 0.7);
}
.fa-ban:hover{
    color:rgba(242, 0, 0, 1);
}
</style>




